import "../scss/style.scss";
// -----------------------------------------Next-----------------------------------------
import Head from "next/head";
import Link from "next/link";
// -----------------------------------------Reactstrap-----------------------------------------
import { Container, Row, Col, Button } from "reactstrap";
// -----------------------------------------React-----------------------------------------
import React from "react";
// -----------------------------------------Components-----------------------------------------
import CenteredHeading from "../components/CenteredHeading"
import Document from "../layouts/document";
// -----------------------------------------Resources-----------------------------------------
import Image from "../images/hero-bg-2.jpg"

export default () => (
	<Document>
		<Row>
			<Col>
				<CenteredHeading picture={ Image } >
					<div className="headers">
						Welcome to the landing page
					</div>
				</CenteredHeading>
			</Col>
		</Row>
		<Row>
			<Col>
				<div class="container-fluid landing-page">
					{/* <!--  Replace logo image with logo svg  --> */ }
					<img src="http://cordine.site/static/media/christopher-logo-white.427e237a.png" class="logo" />
					<div class="row hero">
						<div class="centerContainer">
							<h1 class="quickSand"><span style={ { color: "white" } }>New York</span> Web Developer</h1>
							{/* <!--    On click, add removable tag to search of item clicked, these will be font awesome svgs, with an alt text of the text below    -->
      <p>React, Express, MongoDB, Node, Next, Redux, MySQL, Linux, PHP, Git, Bash, Magento, Wordpress, Bootstrap, Sass, etc...</p>
								<!--    Use search to immediately display related work    --> */}
							<div class="search-container"><input type="search" placeholder="What are you looking for?" /></div>
							<p class="subsearch">Use the search bar above to immediately find <a href="#">related work</a> I've done, or scroll down to view my featured projects and front end design.</p>
							{/* <!--    Enter short links here, just icons with hover text    --> */ }
							<p>GitHub, Codepen, Resume, Projects</p>
						</div>
						<div class="hero-bg"></div>

					</div>
				</div></Col>
		</Row>
		<style jsx>
			{ `
			.landing-page {
  position:relative;
  margin:0;
  padding:0;
  background-color:#111;
}
.hero {
  color:white;
  text-align:center;
  justify-content:center;
  align-items:center;
  height:100vh;
  width:100%;
  z-index:2;
}
.hero * {
  z-index:2;
}
.hero h1 {
  text-shadow:0 2px 5px black;
  color:orange;
}
.centerContainer{
  width:100%;
}
input{
  border:none;
  border-radius:5px;
  background-color:rgba(255,255,255,0);
  border:orange 3px solid;
  color:white;
  padding:.5em;
  box-shadow: 0 2px 5px black;
}
input:focus::placeholder{
  border-radius:5px;
}
input::placeholder{
  color:white;
  text-align:center;
}
.hero-bg {
  position:absolute;
  top:0;
  right:0;
  left:0;
  bottom:0;
  z-index:1;
  background-color:#333;
  background-attachement:fixed;
  background-image:url("https://proxy.duckduckgo.com/iu/?u=https%3A%2F%2Fimages.pexels.com%2Fphotos%2F327308%2Fpexels-photo-327308.jpeg%3Fcs%3Dsrgb%26dl%3Dlight-road-dawn-327308.jpg%26fm%3Djpg&f=1&nofb=1");
  background-position:center;
  background-repeat:no-repeat;
  background-size:cover;
  opacity:.3;
}
.subsearch{
  margin-top:.5em;
}

a {
  color:orange;
}
a:hover{
  color:orange;
}
.logo {
  max-width:50px;
  position:fixed;
  bottom:1em;
  right:1em;
}


/* Fonts */
.oswald {
font-family: 'Oswald', sans-serif;
  
}
.quickSand {
font-family: 'Quicksand', sans-serif;
  
}
.vibes {
font-family: 'Vibes', cursive;
  
}
.shadows {
font-family: 'Shadows Into Light', cursive;
  
}

.elite {
font-family: 'Special Elite', cursive;
  
}
.bangers {
font-family: 'Bangers', cursive;  
}
`}
		</style>
	</Document>
);
